<script>
  import * as components from "../../public/examples";
  import ExampleCode from "./_exampleCode.svelte";
  import Prism from "./Prism.svelte";
  import { Tabs, TabsLink, TabsPage, Details, SmoothBox } from "svelteBricks";
  import { slide } from "svelte/transition";

  export let group,
    fullscreen = false,
    show = false,
    height = "auto",
    files = [];

  const Component = components[group][files[0].replace(".svelte", "")];
  const assets = files.map(filename => {
    return {
      name: filename,
      path: "/examples/" + group + "/" + filename
    };
  });

  let cache = false;
  let code = "";
  let showCode = false;

  async function fetchCode(path) {
    if (!code) {
      const res = await fetch(path);
      code = await res.text();
    }
  }
  async function toggleCode() {
    await fetchCode();
    showCode = !showCode;
  }
</script>

<style>* :global(.example-box) {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border-radius: 0.25rem;
}

* :global(.tabs) {
  background-color: #2c5282;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  color: #fff;
  text-align: right;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
}

* :global(.example) {
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
}

* :global(.example>*) {
  text-align: left;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<div>
  <div class="example-box">
    <div class="text-right bg-gray-200 px-3 py-1">
      <button on:click={toggleCode} class="font-bold text-gray-600">
        {`< >`}
      </button>
    </div>
    <SmoothBox style="transition: height 0.3s; background: #222" widthProp="">
      <Details bind:show={showCode} hideToggle={true}>
        <ExampleCode {assets} />
      </Details>
    </SmoothBox>
    <div class="example relative" style="height: {height}">
      {#if !fullscreen}
        <Component data-tab="component" />
      {:else}
        <button class="btn solid green" on:click={() => (show = true)}>
          Show example
        </button>
        {#if show}
          <div class="fixed inset-0 bg-white">
            <Component data-tab="component" />
            <button
              class="absolute top-0 right-0"
              on:click={() => (show = false)}>
              Close example
            </button>
          </div>
        {/if}
      {/if}
    </div>
  </div>
</div>
