<script>
  import { Snackbar } from "svelteBricks";
  function popASnack() {
    console.info({ snack: "Hello" });
  }
  function popAWarning() {
    console.info({
      snack: "I'm a warning message",
      class: "warning"
    });
  }
</script>

<style>* :global(.topright) {
  position: fixed;
  top: 20px;
  right: 40px;
}

* :global(.snack.warning) {
  background: red;
  color: white;
}

* :global(button) {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  display: inline-flex;
  align-items: center;
  border-radius: 0.25rem;
  background-color: #38a169;
  color: #f0fff4;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<span>
  <Snackbar />
  <Snackbar
    containerClass="topright"
    filter={msg => msg.class === 'warning'}
    time={0}
    closeable={true} />

  <button on:click={popASnack}>Launch snack</button>
  <button on:click={popAWarning}>Launch warning</button>
</span>
